import { init as initForm } from './booking-form';
import { isClickOutside } from '../helpers';

const booking = document.querySelector('.booking');

function hideBooking() {
  booking.classList.remove('booking_show');
  booking.classList.add('booking_hide');
  setTimeout(() => {
    booking.classList.remove('booking_hide');
  }, 400);
}

function handleDocumentClick(e) {
  const isBookingShowed = booking.classList.contains('booking_show');
  const isCloseBtnClicked = e.target.closest('.booking__close-btn') !== null;

  if ((isClickOutside(e, ['booking__container']) && isBookingShowed) || isCloseBtnClicked) {
    hideBooking();
  }
}

export default function init() {
  initForm();

  document.addEventListener('click', handleDocumentClick);
}